.container{
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    width: 100%;
    height: auto;

}
.formContainer{
    width: 90%;
    height:auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    background: #fff;
    padding: 1rem;
    margin-bottom: 2rem;
}

.formBox{
    width: 47%;
    min-height:9rem;
    padding: 1rem ;
    border-bottom: 1px solid #ddd;
    background-color: rgb(254,253,253);
}

.input{
    width: 95%;
    height: 5rem;
    margin: 0;
    padding: 0;
    border: 0px;
    border-bottom: 1px solid #aaa;
}
.label{
    width: 100%;
    text-align: left;
    display: block;
}
.button{
    padding: 0.5rem 2.5rem;
    outline: none;
    border: 1px solid #ddd;
    border-radius: 0.5rem;
    margin-top: 2rem;
}
.button:hover{
    box-shadow: 5px 5px 1px #aaa;
}
.next{
    bottom: 1rem;
    right: 1rem;
}
.prev{
    bottom: 1rem;
    left: 1rem;
}
.button:focus{
    outline: none;
}
.optionBox{
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin : 0 0.5rem;
}
.optionContainerRow{
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.optionContainerCol{
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    
}
.optionContainerRow label, .optionContainerCol label{
    padding: 1rem;
}

.resultContainer{
    width: 90%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}